.pictures {

    

  &__list-items {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;

    img {
      width: 250px;
    }
  }

  &__list-item {
    margin-left: auto;
    margin-right: auto;
  }

  &__title {
    .ma-pa-reset();
    color: #ffffff;
    display: block;
    width: 150px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    text-transform: uppercase;
    padding-top: 150px;
    letter-spacing: 2px;
  }

  @media (min-width: @tablet-width){/////////////////////////////////////////////



     



    &__list-items {
      img {
        width: 450px;
      }
    }


  }


   @media (min-width: @desktop-width){//////////////////////////////////////////
       margin-bottom: 50px;
      
  }
}
