.slider {
  background-color: #ffffff;
  display: flex;

  &__container {
    outline: 1px solid lightgrey;
    display: flex;
    position: relative;
    margin: 0 auto;
    width: 750px;
    overflow: hidden;
  }

  &__item {
    display: flex;
    justify-content: center;
  }

  &__picture-block {
    display: flex;
    align-items: center;
  }

  &__picture {
    max-width: 100%;
    max-height: 366px;
    padding-top: 10px;
    padding-bottom: 30px;
  }

.swiper-container-horizontal >
.swiper-pagination-progressbar,
.swiper-container-vertical >
.swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  height: 2px;
}

 .swiper-pagination-progressbar {
   background-color: lightgrey;
 }

 .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
   background-color: #000000;
 }

 .swiper-pagination-fraction {
   bottom: 5px;
   font-weight: lighter;
 }

 .swiper-pagination-current {

   font-size: 17px;
 }

 .swiper-button-prev,
 .swiper-button-next {
   width: 50px;
   height:50px;
   background-color: #ffffff;
   border-radius: 100%;
   border: 1px solid black;
   opacity: 0.5;
   &:hover {
     opacity: 0.8;
   }

 }


 .swiper-button-next:hover,
  .swiper-button-prev:hover{
   border: 1px solid black;
 }

 .swiper-button-next:active,
  .swiper-button-prev:active{
  &:hover {
    opacity: 0.7;
  }
 }

 .swiper-button-next::after,
  .swiper-button-prev::after {
    font-size: 17px;
    color: #000000;
    width: 50px;
    height:50px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }


  @media (min-width: @tablet-width) { //////////////////////////////////////////
    .swiper-button-prev,
    .swiper-button-next {
      display: block;
      &:hover {
        opacity: 0.8;
      }
    }
  }

  @media (min-width: @desktop-width) { /////////////////////////////////////////

    &__picture{
      max-height: 630px;
      justify-content: center;
      padding-top: 20px;
      margin-bottom: 10px;
    }

    .swiper-button-prev,
    .swiper-button-next {
      display: block;
      &:hover {
        opacity: 0.8;
      }
    }
  }
}
