.main-nav {
  margin: 0 auto;
  background-color: ffffff;
  color: #ffffff;
  position: relative;

  &--closed {
    top: 0;
    left: 0;
    right: 0;
    background-color: ffffff;
  }

  &__logo-link {
    font-family: "Montserrat", "Arial",sans-serif;
    display: block;
    margin-top: 24px;
    margin-bottom: 19px;
    height: 30px;
    font-size: 20px;
    font-weight: 100;
    font-style: italic;
    letter-spacing: 3px;
    color: #000000;
    &:visited {
      color: #000000;
    }
    &[href]:active {
      opacity: 0.3;
    }
    &[href]:hover {

      text-decoration: underline;
    }
  }

  &__toggle {
    position: absolute;
    background-color: transparent;
    border: none;
    width: 28px;
    height: 25px;
    right: 0px;
    top: 22px;
    cursor: pointer;
  }

  &--closed .main-nav__toggle::before {
    content: "";

    position: absolute;
    width: 20px;
    height: 2px;
    right: 4px;
    top: 4px;

    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }

  &--closed .main-nav__toggle:hover::before {
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }

  &--closed .main-nav__toggle:active::before {
    opacity: 0.3;
  }

  &--opened .main-nav__toggle{
    content: "";
    position: absolute;
    right: 0px;
    top: 22px;
    width: 28px;
    height: 25px;
  }

  &--opened .main-nav__toggle::before,
  &--opened .main-nav__toggle::after{
    position: absolute;
    left: 14px;
    top: 1px;
    content: "";
    height: 24px;
    width: 2px;
    background-color: #000000;
  }

  &--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }

  &--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }

  &--opened .main-nav__toggle:hover::before,
  &--opened .main-nav__toggle:hover::after{
    background-color: #000000;
  }

  &--opened .main-nav__toggle:active::before,
  &--opened .main-nav__toggle:active::after{
    opacity: 0.3;
  }

  &__block {
    display: flex;
    justify-content: space-between;
  }

  &__site-list {
    .list-reset ();
    display: flex;
    flex-direction: column;

    font-size: 18px;
    font-weight: bold;
    line-height: 32px;
  }

  @media (min-width: @tablet-width) { ///////////////////////////////////////////


    &__toggle {
      right: 0;
      top: 22px;
    }

    &--opened .main-nav__toggle{
      top: 22px;
      right: 0px;
    }
  }

  @media(max-width: @mobile-and-tablet-width-only) {///////////////////////////
    &--closed .site-list__item {
      display: none;
      &--active {
        display: block;
      }
    }

    &--nojs .main-nav__toggle {
      display: none;
    }

    &--nojs .site-list__item {
      display: block;
    }
  }

  @media (min-width: @desktop-width) {/////////////////////////////////////////


    &__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__block {
      border-bottom: 1px solid #d3d3d3;
    }

    &__logo-link {
      margin-left: auto;
      margin-right: auto;
      font-size: 60px;
      margin-top: 60px;
      margin-bottom: 80px;
    }

    &__toggle {
      display: none;
    }

    &__site-list {
      margin-bottom: 30px;
      margin-top: -28px;
      background-color: #ffffff;
    }
  }
}
