.main-nav {
  margin: 0 auto;
  background-color: ffffff;
  color: #ffffff;
  position: relative;
}
.main-nav--closed {
  top: 0;
  left: 0;
  right: 0;
  background-color: ffffff;
}
.main-nav__logo-link {
  font-family: "Montserrat", "Arial", sans-serif;
  display: block;
  margin-top: 24px;
  margin-bottom: 19px;
  height: 30px;
  font-size: 20px;
  font-weight: 100;
  font-style: italic;
  letter-spacing: 3px;
  color: #000000;
}
.main-nav__logo-link:visited {
  color: #000000;
}
.main-nav__logo-link[href]:active {
  opacity: 0.3;
}
.main-nav__logo-link[href]:hover {
  text-decoration: underline;
}
.main-nav__toggle {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 28px;
  height: 25px;
  right: 0px;
  top: 22px;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  right: 4px;
  top: 4px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle {
  content: "";
  position: absolute;
  right: 0px;
  top: 22px;
  width: 28px;
  height: 25px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  left: 14px;
  top: 1px;
  content: "";
  height: 24px;
  width: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}
.main-nav__block {
  display: flex;
  justify-content: space-between;
}
.main-nav__site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  font-weight: bold;
  line-height: 32px;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    right: 0;
    top: 22px;
  }
  .main-nav--opened .main-nav__toggle {
    top: 22px;
    right: 0px;
  }
}
@media (max-width: 1199px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item--active {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .main-nav__block {
    border-bottom: 1px solid #d3d3d3;
  }
  .main-nav__logo-link {
    margin-left: auto;
    margin-right: auto;
    font-size: 60px;
    margin-top: 60px;
    margin-bottom: 80px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__site-list {
    margin-bottom: 30px;
    margin-top: -28px;
    background-color: #ffffff;
  }
}
