.slider {
  background-color: #ffffff;
  display: flex;
}
.slider__container {
  outline: 1px solid lightgrey;
  display: flex;
  position: relative;
  margin: 0 auto;
  width: 750px;
  overflow: hidden;
}
.slider__item {
  display: flex;
  justify-content: center;
}
.slider__picture-block {
  display: flex;
  align-items: center;
}
.slider__picture {
  max-width: 100%;
  max-height: 366px;
  padding-top: 10px;
  padding-bottom: 30px;
}
.slider .swiper-container-horizontal > .swiper-pagination-progressbar,
.slider .swiper-container-vertical > .swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  height: 2px;
}
.slider .swiper-pagination-progressbar {
  background-color: lightgrey;
}
.slider .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background-color: #000000;
}
.slider .swiper-pagination-fraction {
  bottom: 5px;
  font-weight: lighter;
}
.slider .swiper-pagination-current {
  font-size: 17px;
}
.slider .swiper-button-prev,
.slider .swiper-button-next {
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  border-radius: 100%;
  border: 1px solid black;
  opacity: 0.5;
}
.slider .swiper-button-prev:hover,
.slider .swiper-button-next:hover {
  opacity: 0.8;
}
.slider .swiper-button-next:hover,
.slider .swiper-button-prev:hover {
  border: 1px solid black;
}
.slider .swiper-button-next:active:hover,
.slider .swiper-button-prev:active:hover {
  opacity: 0.7;
}
.slider .swiper-button-next::after,
.slider .swiper-button-prev::after {
  font-size: 17px;
  color: #000000;
  width: 50px;
  height: 50px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 660px) {
  .slider .swiper-button-prev,
  .slider .swiper-button-next {
    display: block;
  }
  .slider .swiper-button-prev:hover,
  .slider .swiper-button-next:hover {
    opacity: 0.8;
  }
}
@media (min-width: 1200px) {
  .slider__picture {
    max-height: 630px;
    justify-content: center;
    padding-top: 20px;
    margin-bottom: 10px;
  }
  .slider .swiper-button-prev,
  .slider .swiper-button-next {
    display: block;
  }
  .slider .swiper-button-prev:hover,
  .slider .swiper-button-next:hover {
    opacity: 0.8;
  }
}
