.block-name {
    width: 150px;
    margin-left: auto;
    margin-right: auto;
  &__title {
    display: block;
    font-family: "Times New Roman", serif;
    font-size: 30px;
    line-height: 40px;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__title--small {
    display: block;
    font-weight: lighter;
    font-size: 20px;
    line-height: 40px;
    text-transform: uppercase;
    color: #ffffff;
    margin-left: auto;
    margin-right: auto;
  }
}
