.contact {

  &__picture-block {
    img {
    background-color: #616f92;
    }
  }

  &__title {
    .ma-pa-reset ();
    font-size: 20px;
    font-weight: 300;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: underline;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 10px;
  }

  &__text {
    .ma-pa-reset ();
    text-align: center;
    font-weight: 300;
    font-size: 15px;
    padding-bottom: 40px;
  }

  &__daten {
    .ma-pa-reset();
    width: 200px;
    margin-left: auto;
    margin-right: auto;

  }

  &__daten-title{
      .ma-pa-reset();
        font-weight: 300;
        font-size: 20px;
        text-align: center;
        padding-bottom: 10px;
        margin-top: -20px;
  }

  &__number {
      display: flex;
      justify-content: center;
      padding-bottom: 10px;
      font-size: 20px;
      color: #000000;
      font-weight: 300;
      &:hover {
        opacity: 0.5;
        text-decoration: underline;
      }
    }

  &__mail {
    display: flex;
    justify-content: center;
    color: #000000;
    font-size: 20px;
    font-weight: 300;
    &:hover {
      opacity: 0.5;
      text-decoration: underline;
    }
  }

  @media (min-width: @tablet-width){/////////////////////////////////////////////

    &__picture-block {
      img {
        width: 100%;
      }
    }

    &__title {
      font-size: 25px;
      padding-bottom: 20px;
    }

    &__text {
      font-size: 20px;
    }

    &__daten {
      width: 250px;
      margin-bottom: 80px;
    }

    &__daten-title{
      font-size: 25px;
    }

    &__number {
      font-size: 25px;
      }

    &__mail {
      font-size: 25px;
      }
    }


   @media (min-width: @desktop-width){//////////////////////////////////////////

    border-bottom: 1px solid #d5d5d5;

    &__picture-block {
      width: 730px;
      margin-left: auto;
      margin-right: auto;
    }

     &__title {
       font-size: 30px;
       padding-bottom: 70px;
       padding-top: 40px;
     }

     &__text {
       font-size: 25px;
       padding-bottom: 60px;
       width: 730px;
        margin-left: auto;
        margin-right: auto;
     }

     &__daten {
       margin-bottom: 80px;
     }

     &__daten-title{
      font-size: 28px;
      line-height: 30px;
     }

     &__number {
      font-size: 28px;
      line-height: 30px;
     }
    }
  }
