.aboutme {
  &__picture-block {
    img {
    background-color: #bfa08c;
  }
}
  &__title {
    .ma-pa-reset ();
    font-weight: 300;
    display: flex;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 15px;
  }

  &__text-block {
    .ma-pa-reset ();
  }

  &__text {
    .ma-pa-reset ();
    font-weight: 300;
    padding-bottom: 10px;
    text-align: justify;
  }

  @media (min-width: @tablet-width){/////////////////////////////////////////////

    &__picture-block {
      img {
      width: 100%;
    }
  }
  &__title {
    font-size: 30px;
    padding-top: 50px;
    padding-bottom: 25px;
  }

  &__text {
    font-size: 18px;
    padding-bottom: 15px;
  }
}

   @media (min-width: @desktop-width){/////////////////////////////////////////
     &__picture-block {
       display: flex;
       flex-direction: row;
       justify-content: center;
       max-width: 730px;
       margin-left: auto;
       margin-right: auto;
       padding-bottom: 80px;
       img {
       width: 100%;
     }
   }

     &__title {
       font-size: 35px;
       .ma-pa-reset();
       padding-bottom: 30px;
     }

     &__text-block {

       width: 80%;
       margin: 0 auto;
       padding-bottom: 70px;
     }

     &__text {
       font-size: 20px;
       padding-bottom: 20px;
     }
   }
}
