.page-header {

  &__wrapper {
    width: 280px;
    padding: 0 20px;
    margin: 0 auto;
    outline: 1px solid #000000;
  }


  @media (min-width: @tablet-width) { ////////////////////////////////////////
    &__wrapper {
      width: 640px;
      padding: 0 64px;
    }

  }
  @media (min-width: @desktop-width) { ///////////////////////////////////////
    &__wrapper {
      width: 960px;
      padding: 0 120px;
    }
    top: 0;
    left: 0;
    right: 0;
  }
}
