.contact-block {
  display: flex;
  justify-content: center;

  &__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    .ma-pa-reset;
  }

  &__link {
    &:hover {
      opacity: 0.5;
    }
  }

  &__item {
    padding-left: 10px;
  }

  @media (min-width: @tablet-width) { ////////////////////////////////////////
    &__item {
      padding-right: 0;
      padding-left: 10px;
    }
  }


  @media (min-width: @desktop-width) { ///////////////////////////////////////
    &__list {
      margin-top: 0px;
    }

  }

}
