.site-list {
  display: flex;
  flex-wrap: wrap;
  min-width: 220px;
  margin-left: auto;

  &__link {
    display: block;
    font-family: "Montserrat", "Arial", sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: left;
    padding-top: 16px;
    padding-bottom: 14px;
    color: #000000;
    font-weight: 100;

    &:visited {
      color: #000000;
    }

    &--active {
      opacity: 0.5;
    }

    &[href]:hover {
      opacity: 0.5;
    }
  }

  @media (min-width: @tablet-width) {/////////////////////////////////////////
    width: 100%;

    &__link {
      text-align: center;
      border-bottom: 1px solid #000000;

    }
  }

  @media (min-width: @desktop-width) {////////////////////////////////////////

    flex-direction: row;
    justify-content: space-around;
    width: 700px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 20px;


    &__item {
      border-bottom: 0;
      border-top: 0;
    }

    &__link {
      padding: 0;
      padding-bottom: 5px;
      margin-top: 10px;
      border-bottom: 0;
      border-top: 0;


      &--active {
        border-bottom: 1px solid #d3d3d3;
        opacity: 1;
      }
    }
  }
}
