.container {

    display: flex;
    flex-direction: column;
    width: 320px;
    min-height: 100vh;
    padding: 0 20px;
    margin: 0 auto;

  @media (min-width: @tablet-width) { ////////////////////////////////////////
      width: 580px;
      padding: 0 64px;
  }

  @media (min-width: @desktop-width) { ///////////////////////////////////////
      width: 1160px;
      padding: 0 120px;
      background-color: #ffffff;
      outline: 1px solid #d3d3d3;
  }
}
