.page-main {


  &__pictures {
    background-image: url("../img/collage-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-bottom: 20px;
    height: 700px;
  }
  &__wrapper {
    width: 280px;
    margin: 0 auto;
    padding: 0 20px;
    outline: 1px solid #000000;
  }

  &__block-name {
    margin-top: -400px;
  }



  &__link {
    font-weight: 300;
    outline: 1px solid #000000;
    padding: 5px 30px;
    &:hover {
      color: #ffffff;
      background-color: #d5d5d5;
      outline: none;
    }

    &:focus {
      opacity: 0.7;
    }

    &:visited {
      color: #000000;
    }
}

&__link-block {
  display: flex;
  justify-content: center;
  .ma-pa-reset();
  margin-bottom: 20px;
}

  @media (min-width: @tablet-width) { //////////////////////////////////////////

    &__pictures {
      background-image: url("../img/collage-tablet@1x.jpg");
    }


}

  @media (min-width: @desktop-width) { /////////////////////////////////////////
    &__pictures {
    background-image: url("../img/collage-desktop@1x.jpg");
      }
    border-bottom: 1px solid #d3d3d3;

    &__pictures-link {

      margin-top: 15px;
      padding: 5px 20px;
      }
  }
}
